import React,{Component} from 'react';
import axios from 'axios'
import {Button, Table, Input, Modal, Tabs, Form, Row, Select, message, Card, AutoComplete, Tooltip} from "antd";
import { Link } from 'react-router-dom';
import "antd/dist/antd.css";
import "../../styles/index.scss"
import { backendServerApiRoot } from "../common/util"
axios.defaults.withCredentials = true;
axios.defaults.headers.post['Content-Type'] = 'application/json';
const { TextArea } = Input
const { TabPane } = Tabs;
const Column = Table.Column;
const FormItem = Form.Item;
const { Option } = Select;


class AuditSqlIndex extends Component {
    constructor(props) {
        super(props);
        this.state = {
            expand:false,
            selectEnvOptionData:['',''],
            selectExecuteTypeOptionData:['',''],
            des_ip:"",
            des_port:"",
            check_sql:"",
            check_sql_info:[],
            check_sql_results:[],
            showDataVisible: false,
            submit_sql_info:[],
            submit_sql_button_disabled:"hide",
            submit_sql_flag:"",
            sql_check_loading:false,
            sql_submit_loading:false,
            des_ip_list:[],
            cluster_name_list:[],
            submit_source_db_type:"cluster",
            cluster_name:"",
            schema_name:"",
            table_name:"",
            min_id:"",
            max_id:"",
            where_condition:"",
            mdl_type:"delete",
            create_sql_content:"",
            rebuild_table:"",
            set_value:"",

        }
    }


    componentDidMount() {
        this.GetSubmitSqlInfo()
    }
    //SQL
    async GetSubmitSqlInfo() {
        let res = await axios.get(`${backendServerApiRoot}/get_submit_sql_info/`,);
        this.setState({
            submit_sql_info: res.data.data,
        });
    }
    //SQL,cluster_namemaster_ip_port
    async handleSqlCheck() {
        this.state.submit_source_db_type==="cluster" ? this.handleClusterNameSqlCheck(): this.handleMasterIpPortSqlCheck()
    }

    //SQL,delete/update
    async handlerCreateBlockSql() {
        this.setState({create_sql_content:""})
        let params = {
                db_ip: this.state.des_ip,
                db_port: this.state.des_port,
                cluster_name:this.state.cluster_name,
                schema_name:this.state.schema_name,
                table_name:this.state.table_name,
                min_id:this.state.min_id,
                max_id:this.state.max_id,
                where_condition: this.state.where_condition,
                mdl_type: this.state.mdl_type,
                rebuild_table: this.state.rebuild_table,
                set_value: this.state.set_value,
            };
        console.log(params)
        await axios.post(`${backendServerApiRoot}/create_block_sql/`,{params}).then(
                res => {res.data.status==="ok"?
                    this.setState({
                        create_sql_content: res.data.data,
                    })
                    :
                    message.error(res.data.message,3) && this.setState({create_sql_content: ""})
                }
            ).catch(err => {
                message.error(err, 3);
                this.setState({
                    create_sql_content: "",
                });
            })
    }
    //cluster_nameSQL,SQL
    async handleClusterNameSqlCheck() {
        if (this.state.cluster_name.length===0 || this.state.check_sql.length===0){
            message.error("")
        }else{
            let params = {
                db_ip: this.state.des_ip,
                db_port: this.state.des_port,
                check_sql_info: this.state.check_sql,
                cluster_name:this.state.cluster_name,
                submit_source_db_type:this.state.submit_source_db_type
            };
            this.setState({
                check_sql_results: [],
                sql_check_loading:true,
                submit_sql_button_disabled:"hide"
            });
            await axios.post(`${backendServerApiRoot}/check_sql/`,{params}).then(
                res => {res.data.status==="ok"?
                    this.setState({
                        check_sql_results: res.data.data,
                        submit_sql_button_disabled:"show",
                        sql_check_loading:false,
                    })
                    :
                    message.error(res.data.message,3) && this.setState({check_sql_results: [],sql_check_loading:false,})
                }
            ).catch(err => {
                message.error(err, 3);
                this.setState({
                    check_sql_results: [],
                    sql_check_loading:false,
                });
            })
        }
    }
    //master_ip_portSQL,ipportSQL
    async handleMasterIpPortSqlCheck() {
        if (this.state.des_ip.length===0 || this.state.des_port.length===0 || this.state.check_sql.length===0){
            message.error("")
        }else{
            let params = {
                db_ip: this.state.des_ip,
                db_port: this.state.des_port,
                check_sql_info: this.state.check_sql,
                cluster_name:this.state.cluster_name,
                submit_source_db_type:this.state.submit_source_db_type
            };
            this.setState({
                check_sql_results: [],
                sql_check_loading:true,
                submit_sql_button_disabled:"hide"
            });
            await axios.post(`${backendServerApiRoot}/check_sql/`,{params}).then(
                res => {res.data.status==="ok"?
                    this.setState({
                        check_sql_results: res.data.data,
                        submit_sql_button_disabled:"show",
                        sql_check_loading:false,
                    })
                    :
                    message.error(res.data.message,3) && this.setState({check_sql_results: [],sql_check_loading:false,})
                }
            ).catch(err => {
                message.error(err, 3);
                this.setState({
                    check_sql_results: [],
                    sql_check_loading:false,
                });
            })
        }
    }

    //SQL,
    handleSubmit = e => {
        e.preventDefault();
        this.props.form.validateFields((err, values) => {
            console.log('Received values of form: ', values,this.state.check_sql_results);
            values["check_sql_results"] = this.state.check_sql_results;
            this.state.submit_sql_flag === "" ? this.handleSqlSubmit(values) : message.error(this.state.submit_sql_flag)
        });
    };
    //SQL
    async handleSqlSubmit(value) {
        this.setState({
            sql_submit_loading:true
        });
        let params = {
            submit_source_db_type:this.state.submit_source_db_type,
            cluster_name:this.state.cluster_name,
            db_ip: this.state.des_ip,
            db_port: this.state.des_port,
            check_sql: this.state.check_sql,
            title:value["Title"],
            env:value["ENV"],
            info:value["INFO"],
            check_sql_results: value["check_sql_results"],
            submit_sql_execute_type: value[""],
            comment_info: value["comment_info"],
        };
        let res = await axios.post(`${backendServerApiRoot}/submit_sql/`,{params});
        if( res.data.status === 'ok'){
            window.location.reload();
        }
        else
            alert(res.data.message);
    }

    //master ip
    onSearch = searchText => {
        this.handleGetMasterIp(searchText)
    };
    //master ipip
    async handleGetMasterIp(value) {
        if (value.length>=1){
            let params = {
                db_master_ip_or_hostname: value
            };
            let res = await axios.post(`${backendServerApiRoot}/get_master_ip/`,{params});
            if( res.data.status === 'ok'){
                this.setState({
                    des_ip_list: res.data.data.length===0 ? []:res.data.data,
                });
            } else{
                console.log("get_master_ip")
            }
        }else {
            this.setState({
                des_ip_list: []
            });
        }
    }
    //cluster_name
    onClusterNameSearch = searchText => {
        this.handleGetClusterName(searchText)
    };
    //cluster_nameip
    async handleGetClusterName(value) {
        if (value.length>=1){
            let params = {
                cluster_name: value
            };
            let res = await axios.post(`${backendServerApiRoot}/get_cluster_name/`,{params});
            if( res.data.status === 'ok'){
                this.setState({
                    cluster_name_list: res.data.data.length===0 ? []:res.data.data,
                });
            } else{
                console.log("get_cluster_name")
            }
        }else {
            this.setState({
                cluster_name_list: []
            });
        }
    }
    //ip
    onSelect = select => {
        this.setState({
            des_ip:select
        })
    };
    //cluster_name
    onClusterNameSelect = select => {
        this.setState({
            cluster_name:select
        })
    };
    //master port
    handleHostPortChange = (value) => {
        this.setState({
            des_port: value
        })
    }
    //cluster name
    handleClusterNameChange = (value) => {
        this.setState({
            cluster_name: value
        })
    }
    //sql
    handleSqlChange = (value) => {
        this.setState({
            check_sql: value,
            check_sql_results:[],
        })
    }
    //modal
    showDataModalHandle = (e) => {
        if (this.state.check_sql.length===0|| this.state.check_sql_results.length===0){
            message.error("")
        }else {
            this.setState({
                showDataVisible: true
            });
        }
    }
    //modal
    showDataHandleCancel = (e) => {
        this.setState({
            showDataVisible: false,
        });
    }
    //
    handleDbSourceTypeChange = (value) => {
        this.setState({
            submit_source_db_type: value,
        });
    }

    handleReset = () => {
      this.props.form.resetFields();
    };

    toggle = () => {
      const { expand } = this.state;
      this.setState({ expand: !expand });
    };
    render() {
              const check_results_columns = [
            {
              title: 'ID',
              dataIndex: 'ID',
              key: "ID",
            },
            {
              title: 'SQL',
              dataIndex: 'SQL',
              key: "SQL",
              width:520
            },
            {
              title: '',
              dataIndex: 'Stage_Status',
              key:"Stage_Status",
            },
            {
              title: '',
              dataIndex: 'Error_Level',
              key:"Error_Level",
              sorter: (a, b) => a.Error_Level - b.Error_Level,
            },
            {
              title: '',
              dataIndex: 'Error_Message',
              key:"Error_Message",
            },
            {
              title: '',
              dataIndex: 'Affected_rows',
              key:"Affected_rows",
            }
        ];
        const {form} = this.props;
        const {getFieldDecorator} = form;
        return (
            <Tabs className="container">
                <TabPane tab="SQL" key="1">
                    <Table
                        dataSource={this.state.submit_sql_info}
                        rowKey={(row ,index) => index}
                        size="small"
                    >
                        <Column title=""
                            dataIndex="title"
                            render={(text) => {
                                return (
                                    text.length>5 ?
                                    <Tooltip placement="topLeft" title={text}>
                                        <span>{text.slice(0,5)}...</span>
                                    </Tooltip>
                                        :<span>{text}</span>
                                )
                            }}
                        />
                        <Column title=""
                                dataIndex="submit_sql_user"/>
                        <Column title="TL"
                            dataIndex="leader_user_name"/>
                        <Column title="QA"
                            dataIndex="qa_user_name"/>
                        <Column title="TL"
                                dataIndex="leader_check"
                                render={(val) => {
                                return <span>{val==="" ? <span style={{color:"green"}}>{val}</span>:<span style={{color:"red"}}>{val}</span>}</span>
                                }}
                        />
                        <Column title="QA"
                                dataIndex="qa_check"
                                render={(val) => {
                                    return <span>{val==="" ? <span style={{color:"green"}}>{val}</span>:<span style={{color:"red"}}>{val}</span>}</span>
                                }}
                        />
                        <Column title="DBA"
                                dataIndex="dba_check"
                                render={(val) => {
                                    return <span>{val==="" ? <span style={{color:"green"}}>{val}</span>:<span style={{color:"red"}}>{val}</span>}</span>
                                }}
                        />
                        <Column title="DBA"
                                dataIndex="dba_execute"
                                render={(val) => {
                                    return <span>{val==="" ? <span style={{color:"green"}}>{val}</span>:<span>{val}</span>}</span>
                                }}
                        />
                        <Column title=""
                                dataIndex="dba_execute_user_name"/>
                        <Column title=""
                                dataIndex="execute_status"
                                render={(val) => {
                                    return <span>{val==="" || val===""? <span>{val}</span>:<span  style={{color:"red"}}>{val}</span>}</span>
                                }}
                        />
                        <Column title=""
                            dataIndex="ctime"/>
                        <Column title=""
                            dataIndex="utime"/>
                        <Column title=""
                            render={record => {
                                return <Link to={`/viewApplySqlByUuid/${record.submit_sql_uuid}`}></Link>
                            }}/>
                    </Table>
                </TabPane>
                <TabPane tab="SQL" key="2">
                    <div className="sub-title-input">
                        <Select defaultValue="" style={{ width: 150 }} onChange={e => this.handleDbSourceTypeChange(e)}>
                            <Option value="cluster"></Option>
                            <Option value="master_ip">master_ip_port</Option>
                            <Option value="template"></Option>
                        </Select>
                        {
                            this.state.submit_source_db_type==="cluster" ?
                                <div>
                                    <AutoComplete
                                        dataSource={this.state.cluster_name_list}
                                        style={{ width: 300,marginLeft:10 }}
                                        onSelect={this.onClusterNameSelect}
                                        onSearch={this.onClusterNameSearch}
                                        placeholder=""
                                        size="default"
                                    />
                                </div>
                            :
                                <div>
                                    <AutoComplete
                                        dataSource={this.state.des_ip_list}
                                        style={{ width: 300,marginLeft:10 }}
                                        onSelect={this.onSelect}
                                        onSearch={this.onSearch}
                                        placeholder="ip"
                                        size="default"
                                    />
                                    <Input size="default" style={{marginLeft:10, width: 300}} placeholder="" onChange={e => this.handleHostPortChange(e.target.value)}/>
                                </div>
                        }
                    </div>
                    <div>
                        <TextArea rows={10} placeholder="SQL,SQL ; "  onChange={e => this.handleSqlChange(e.target.value)}/>
                        <Button type="primary" loading={this.state.sql_check_loading} onClick={()=>{this.handleSqlCheck()}}>SQL</Button>
                        {this.state.submit_sql_button_disabled==="show" ? <Button  style={{marginLeft:10}} type="primary" onClick={()=>{this.showDataModalHandle()}}>SQL</Button>:null}
                    </div>
                    <Table
                          dataSource={this.state.check_sql_results}
                          rowKey={(row ,index) => index}
                                                    rowClassName={(record, index) => {
                                                let className = 'row-detail-default ';
                                                if (record.Error_Level === 2) {
                                                    className = 'row-detail-error';
                                                    return className;
                                                }else if (record.Error_Level  === 0){
                                                    className = 'row-detail-success';
                                                    return className;
                                                }else if (record.Error_Level  === 1){
                                                    className = 'row-detail-warning';
                                                    return className;
                                                }else {
                                                    return className;
                                                }
                                    }}
                            pagination={true}
                            size="small"
                            columns={check_results_columns}

                      />
                    <Modal
                        title=""
                        visible={this.state.showDataVisible}
                        onCancel={this.showDataHandleCancel}
                        onOk={this.handleSqlSubmit.bind(this)}
                        width='960px'
                        footer={false}
                    >
                        <Form className="ant-advanced-search-form" labelCol={{ span: 2 }} onSubmit={this.handleSubmit}>
                            <Row gutter={24}>
                                <Card>
                                    <FormItem  label='Title'>
                                        {getFieldDecorator('Title', {rules: [{required: true, message: 'title'}],})(
                                            <Input placeholder='title'/>
                                        )}
                                    </FormItem>
                                    <FormItem  label='ENV'>
                                        {getFieldDecorator('ENV', {rules: [{required: true, message: ''}],})(
                                            <Select>
                                                {this.state.selectEnvOptionData.map((record,index) => <Select.Option key={index} value={record}>{record}</Select.Option>)}
                                            </Select>
                                        )}
                                    </FormItem>
                                    <FormItem  label=''>
                                        {getFieldDecorator('', {rules: [{required: true, message: ''}],})(
                                            <Select>
                                                {this.state.selectExecuteTypeOptionData.map((record,index) => <Select.Option key={index} value={record}>{record}</Select.Option>)}
                                            </Select>
                                        )}
                                    </FormItem>
                                    <FormItem  label=''>
                                        {getFieldDecorator('comment_info', {rules: [{required: true, message: 'comment_info'}],})(
                                            <Input placeholder=''/>
                                        )}
                                    </FormItem>
                                </Card>
                                <Button type="primary" loading={this.state.sql_submit_loading} htmlType="submit">submit</Button>
                            </Row>
                        </Form>
                    </Modal>
                </TabPane>
                <TabPane tab="SQL" key="3">
                    <div className="sub-title-input">
                        <Input size="default" style={{ width: 150}} placeholder="" onChange={e => this.setState({schema_name:e.target.value})}/>
                        <Input size="default" style={{marginLeft:10, width: 300}} placeholder="" onChange={e => this.setState({table_name:e.target.value})}/>
                    </div>
                    <div className="sub-title-input">
                        <Input size="default" style={{ width: 150}} placeholder="ID" onChange={e => this.setState({min_id:e.target.value})}/>
                        <Input size="default" style={{marginLeft:10, width: 300}} placeholder="ID" onChange={e => this.setState({max_id:e.target.value})}/>
                    </div>
                    <div className="sub-title-input">
                        <Select defaultValue="delete" style={{ width: 150 }} onChange={e => this.setState({mdl_type:e})}>
                            <Option value="delete">delete</Option>
                            <Option value="update">update</Option>
                        </Select>
                        {
                            this.state.mdl_type === "delete" ?
                                <Select defaultValue="" style={{marginLeft:10, width: 150 }} onChange={e => this.setState({rebuild_table:e})}>
                                    <Option value=""></Option>
                                    <Option value=""></Option>
                                </Select>
                            :null
                        }
                    </div>
                    <div className="sub-title-input">
                        {this.state.mdl_type === "update" ? <Input size="default" style={{width: 800}} placeholder="set (set),:name='', age=18" onChange={e => this.setState({set_value:e.target.value})}/> :null}
                    </div>
                    <div className="sub-title-input">
                        <Input size="default" style={{width: 800}} placeholder="where (where,),:name='' and age=18" onChange={e => this.setState({where_condition:e.target.value})}/>
                    </div>
                    <div className="sub-title-input">
                        <Button type="primary" loading={this.state.sql_check_loading} onClick={()=>{this.handlerCreateBlockSql()}}>SQL</Button>
                    </div>
                    <div className="sub-title-input">
                        {this.state.create_sql_content === "" ? null : <TextArea rows={5} value={this.state.create_sql_content} onChange={e => this.setState({where_condition:e.target.value})}/>}
                    </div>
                    <br/>
                    <div><span style={{color:"#fa541c"}}>1idSQL,(id)</span></div>
                    <div><span style={{color:"#fa541c"}}>2delete,</span></div>
                </TabPane>
            </Tabs>
        );
    }
}
export default Form.create()(AuditSqlIndex);